@import "../node_modules/bootstrap/scss/functions";

// variables
// primarysecondary
$primary: #000000;
$secondary: #3A3B3C;
$header: #232626;
// $gray-700: #596878;

@import "../node_modules/bootstrap/scss/variables";

// fs-7fs-8
$font-sizes: map-merge(
  $font-sizes,
  (
    // 7: 0.9rem,
    // 8: 1.1rem,
    // 20: 20px,
  )
);

// bg-main
$theme-colors: map-merge(
  $theme-colors,
  (
    "main": #000000,
    "header": #232626,
    "search": #9AA3AF,
    "search-bg": #3A3B3C,
    "icon": #3D4042,
    "icon-active": #323436,
    "icon-block": #303032,
    "blue": #2F89FF,
    
    // "gray-200": $gray-200,
    // variables: $gray-200
  )
);

@import "../node_modules/bootstrap/scss/maps";
@import "../node_modules/bootstrap/scss/mixins";
@import "../node_modules/bootstrap/scss/utilities";

// utilitieswidth()
$utilities: map-merge(
  $utilities,
  (
    "width":
      map-merge(
        map-get($utilities, "width"),
        (
          responsive: true,
          values:
            map-merge(
              map-get(map-get($utilities, "width"), "values"),
              (
                //  w-40px
                "32px": 32px,
                "40px": 40px,
                "240px": 240px
              )
            ),
        )
      ),
    "height":
      map-merge(
        map-get($utilities, "height"),
        (
          responsive: true,
          values:
            map-merge(
              map-get(map-get($utilities, "height"), "values"),
              (
                //  h-40px
                "32px": 32px,
                "40px": 40px,
                "55px": 55px,
              )
            ),
        )
      ),
  )
);

// utilitiescursetransformtransition()
$utilities: map-merge(
  $utilities,
  (
    "cursor": (
      // responsive: true,
      property: cursor,
      class: cursor,
      values: pointer,
      // classcursor-pointer
    ),
    "transform": (
      // responsive: true,
      property: transform,
      values: (
        rotate-180: rotate(180deg),
        // js classList.add("transform-rotate-180")
      ),
    ),
    "transition": (
      // responsive: true,
      property: transition,
      values: (
        transform: transform 0.2s,
        // classtransition-transform
      ),
    ),
  )
);

@import "../node_modules/bootstrap/scss/bootstrap";
